
.card {
    background: #ffffff;
    margin: 1.5rem 1.0rem;
    position: relative;
    width: 192px;
    -webkit-box-shadow: 6px 6px 2px 0px #666666, 0px 0px 1px 0px #222222;
            box-shadow: 6px 6px 2px 0px #666666, 0px 0px 1px 0px #222222;
}

.App-card {
    -webkit-animation: App-card-spin infinite 20s linear;
            animation: App-card-spin infinite 20s linear;
}

@-webkit-keyframes App-card-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes App-card-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}




